import {Directive, ElementRef, HostListener, Input} from '@angular/core';
import {AdaptAnimateScrollService} from './scroll.service';

@Directive({
  selector: '[adaptScrollToElement]'
})
export class AdaptScrollToElementDirective {

  @Input() scrollDuration: number;

  hash: string = undefined;

  constructor(private scrollToService: AdaptAnimateScrollService, private elem: ElementRef) {
    this.hash = elem.nativeElement.hash.slice(1);
  }

  @HostListener('mousedown')
  onMouseClick(): void {
    this.scrollToService.scrollToElement(this.hash, this.scrollDuration, 0);
  }
}
